ng-sms-editor {
  display: block;
  width: 100%;
  min-height: 150px; }
  ng-sms-editor .ql-editor {
    min-height: 150px;
    font-size: 14px;
    padding: 12px;
    outline: 0;
    line-height: 1.5;
    color: #333333; }
    ng-sms-editor .ql-editor mark {
      color: #45a3f8;
      background-color: transparent; }
    ng-sms-editor .ql-editor p {
      white-space: pre-wrap;
      margin: 0; }
  ng-sms-editor .ql-clipboard {
    left: -100000px;
    height: 1px;
    overflow-y: hidden;
    position: absolute;
    top: 50%; }

.ng-sms-editor {
  position: relative;
  width: 580px;
  border: 1px solid #d3d4e2;
  box-sizing: border-box; }
  .ng-sms-editor *, .ng-sms-editor *:before, .ng-sms-editor *:after {
    box-sizing: border-box; }
  .ng-sms-editor a {
    color: #45a3f8;
    text-decoration: none;
    transition: all 0.2s ease; }
    .ng-sms-editor a:hover {
      color: #2b81ec;
      border-color: #2b81ec; }
  .ng-sms-editor__header {
    display: flex;
    padding: 10px 16px;
    line-height: 20px;
    background-color: #eff0f9;
    font-size: 12px; }
    .ng-sms-editor__header .sms-text {
      margin-right: 8px; }
    .ng-sms-editor__header .sms-label {
      height: 20px;
      line-height: 20px;
      padding-left: 8px;
      padding-right: 8px;
      border: 1px solid #45a3f8;
      border-radius: 3px;
      margin-right: 6px; }
  .ng-sms-editor__footer {
    display: flex;
    padding: 0 16px;
    line-height: 40px;
    background-color: #eff0f9;
    font-size: 12px; }
    .ng-sms-editor__footer .sms-total em {
      color: #f22821;
      font-style: normal; }
    .ng-sms-editor__footer .sms-rule {
      position: relative;
      color: #45a3f8;
      cursor: pointer;
      padding-top: 12px;
      padding-left: 2px; }
      .ng-sms-editor__footer .sms-rule:hover .sms-tooltip {
        display: block; }
      .ng-sms-editor__footer .sms-rule .icon-info {
        display: block;
        width: 16px;
        height: 16px;
        background: url("data:image/png;base64,") no-repeat; }
    .ng-sms-editor__footer .sms-tooltip {
      display: none;
      position: absolute;
      top: -165px;
      left: -126px;
      width: 260px;
      text-align: left;
      padding-bottom: 12px;
      z-index: 12; }
      .ng-sms-editor__footer .sms-tooltip-arrow {
        position: absolute;
        bottom: 12px;
        left: 130px;
        z-index: 2;
        display: block; }
        .ng-sms-editor__footer .sms-tooltip-arrow:before {
          content: "";
          position: absolute;
          top: -5px;
          left: 0;
          z-index: 1;
          width: 10px;
          height: 10px;
          display: block;
          border-bottom: 1px solid #d3d4e2;
          border-right: 1px solid #d3d4e2;
          transform: rotate(45deg);
          background: #fff; }
      .ng-sms-editor__footer .sms-tooltip-inner {
        border-color: #d3d4e2;
        background: #fff;
        color: #999;
        border-radius: 3px;
        padding: 12px;
        max-height: 152px;
        overflow-y: auto;
        border-width: 1px;
        border-style: solid;
        font-size: 12px;
        line-height: 20px; }
        .ng-sms-editor__footer .sms-tooltip-inner p {
          margin: 0; }
  .ng-sms-editor .sms-custom {
    flex: 1; }

.ui-sms-preview, .ui-sms-preview .icon-sms-tail {
  background: url("data:image/png;base64,") no-repeat; }

.ui-sms-preview {
  font-size: 14px;
  position: absolute;
  padding: 88px 32px 0 72px;
  right: -300px;
  top: 0;
  height: 356px;
  box-sizing: border-box;
  width: 284px; }
  .ui-sms-preview .ui-sms-content {
    margin: 0 auto;
    padding: 8px 12px;
    min-height: 125px;
    max-height: 220px;
    line-height: 23px;
    border-radius: 4px;
    background-color: #dfe0e9;
    word-break: break-all;
    overflow-y: auto; }
  .ui-sms-preview .icon-sms-tail {
    display: block;
    margin: -8px 0 0 32px;
    width: 24px;
    height: 16px; }
  .ui-sms-preview em {
    color: #333;
    font-weight: bold;
    font-style: normal; }
  .ui-sms-preview:after {
    content: "";
    position: absolute;
    bottom: 0;
    right: -16px;
    padding: 0 48px;
    width: 100%;
    border-bottom: 1px solid #d3d4e2; }
